.displayForecast {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    gap: 20px;
    padding: 80px;
    height: 100%;
}

.displayForecast a {
    text-decoration: none;
    color: #fff;
    font-size: 2em;
    font-weight: 300;
    text-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.days {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    flex-wrap: nowrap;
    gap: 20px;
    padding: 0;
    border-radius: 2px;
    width: 100%;
}

.days .day {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    background: rgba(20, 20, 20, 0.5);
    color: #fff;
    font-weight: 300;
    font-size: 1.2em;
    text-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    border-radius: 2px;
    padding: 20px;
    width: 100%;
}

.day .astro {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
    padding: 10px;
    border-radius: 2px;
    width: 100%;
}

.day .astro a {
    text-decoration: none;
    color: #fff;
    font-size: 1.2em;
    font-weight: 300;
    text-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.hourly {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    flex-wrap: nowrap;
    gap: 40px;
    padding: 20px;
    border-radius: 2px;
    width: 100%;
}

.hourly .hours {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    flex-wrap: nowrap;
    gap: 40px;
    padding: 20px;
    border-radius: 2px;
    width: 100%;
    height: 30vh;
    overflow-y: scroll;
}

.hourly .hours .hour {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    color: #fff;
    font-weight: 300;
    font-size: 1.2em;
    width: 100%;
    gap: 20px;
    padding: 10px;
}

.hourly .hours .hour .hourDisplay {
    font-size: 2rem;
}

.hourly .hours .hour a {
    text-decoration: none;
    color: #fff;
    font-size: 1.2em;
    font-weight: 300;
}

@media screen and (max-width: 1000px) {
    .displayForecast {
        padding: 10px;
    }

    .days {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 15px;
    }

    .days .day {
        width: 100%;
    }

    .hourly {
        padding: 0px;
    }
}